.boton-contenedor{
  height: 83px;
  display: flex;
  /*el espacio se distribuye de manera proporcional tanto ancho como alto para los elementos*/
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: white;
  border-radius: 25px;
  border: 2px solid white;
  margin: 4px;
  cursor: pointer;
  /* Que el texto no se pueda seleccionar */
  user-select: none;
}

.boton-contenedor:hover{
  background-color: #5a01a7;
}

.operador{
  background-color: #00471b;
}

.operador:hover{
  background-color: #ff6600;
}